import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ApiService, SweetAlertService } from '@services';
import { Company } from '@models';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource, MatTable } from '@angular/material/table';
import { ENDPOINTS } from '@shared';
import { MatSort } from '@angular/material/sort';
import { Router } from '@angular/router';
import { AddressService } from '../../../../core/services/address/address.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-list-company',
  templateUrl: './list-company.component.html',
  styleUrls: ['./list-company.component.scss']
})
export class ListCompanyComponent implements OnInit, AfterViewInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Company>;
  dataSource: MatTableDataSource<Company>;
  displayedColumns: string[] = ['companyName', 'membersAffiliation', 'companyTelephoneNo', 'companyTin', 'country', 'completeAddress', 'delinquent', 'status', 'action'];

  companyList = [];
  regionsList = [];
  provinceList = [];
  cityMunicipalityList = [];
  barangayList = [];

  constructor(
    private apiService: ApiService,
    private addressService: AddressService,
    private sweetAlertService: SweetAlertService,
    private router: Router
  ) {

  }

  ngOnInit(): void {
    this.getList();
  }

  ngAfterViewInit(): void {
   // this.dataSource.sort = this.sort;
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.company).subscribe((res: any) => {
      // res.responseData.data.forEach(element => {
      // this.addressService.getBarangay(element.barangay.barangayId).subscribe(
      //   (res: any) => {
      //     console.log(res.responseData.data);

      //     element.address = res.responseData.data.cityMunicipality.province.region.regionName + " - " +
      //       res.responseData.data.barangayName + " " +
      //       res.responseData.data.cityMunicipality.cityMunicipalityName + ", " +
      //       res.responseData.data.cityMunicipality.province.provinceName;
      //   });
      // });

      this.companyList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Company>(res.responseData.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }


  viewCompany(data: any): void {
    this.router.navigate(['asc/page/company/update/', data.id]);
  }

  deleteCompany(companyParam) {

    Swal.fire({
      text: 'Are you sure you want to delete this company?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.company, companyParam.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Company successfully deleted.');
            this.getList();
          }
        );
      }
    });
  }

}
